import Vue from 'vue'
import Router from 'vue-router'

// Containers
const TheContainer = () => import('@/containers/TheContainer')

// Dashboard
const Beranda = () => import('@/views/Beranda')

// Anggota
const Anggota = () => import('@/views/anggota/Anggota')
const PenggunaAplikasi = () => import('@/views/anggota/PenggunaAplikasi')

// Manajemen
const ManajemenAkses = () => import('@/views/manajemen/ManajemenAkses')
const ManajemenRole = () => import('@/views/manajemen/ManajemenRole')

// Laporan
const Perkara = () => import('@/views/laporan/Perkara')
const Produktivitas = () => import('@/views/laporan/Produktivitas')
const Rekap = () => import('@/views/laporan/Rekap')

// Dpo / Dpb
const Dpo = () => import('@/views/dpo-dpb/Dpo')
const Dpb = () => import('@/views/dpo-dpb/Dpb')

// Kategori
const LayananDarurat = () => import('@/views/kategori/LayananDarurat')
const DaftarWilayah = () => import('@/views/kategori/DaftarWilayah')
const StrukturOrganisasi = () => import('@/views/kategori/StrukturOrganisasi')

// Views - Pages
const Page404 = () => import('@/views/pages/Page404')
const Page500 = () => import('@/views/pages/Page500')
const Login = () => import('@/views/pages/Login')
const Register = () => import('@/views/pages/Register')
const LoginLockApps = () => import('@/views/pages/LoginLockApps')

Vue.use(Router)

function guardMyroute(to, from, next)
{
 var isAuthenticated= false;
//this is just an example. You will have to find a better or 
// centralised way to handle you localstorage data handling 
if(localStorage.getItem('LoggedUser'))
  isAuthenticated = true;
 else
  isAuthenticated= false;
 if(isAuthenticated) 
 {
  next(); // allow to enter route
 } 
 else
 {
  next('/pages/login'); // go to '/login';
 }
}

export default new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  routes: configRoutes()
})

function configRoutes () {
  return [
    {
      path: '/',
      redirect: '/beranda',
      name: 'Beranda',
      beforeEnter : guardMyroute,
      component: TheContainer,
      children: [
        {
          path: 'beranda',
          name: 'Beranda',
          component: Beranda
        },
        {
          path: 'anggota',
          redirect: '/anggota/daftar-anggota',
          name: 'Anggota',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'daftar-anggota',
              name: 'Daftar Anggota',
              component: Anggota
            },
            {
              path: 'pengguna-aplikasi',
              name: 'Pangguna Aplikasi',
              component: PenggunaAplikasi
            }
          ]
        },
        {
          path: 'manajemen',
          redirect: '/manajemen/manajemen-akses',
          name: 'Manajemen Akses',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'manajemen-akses',
              name: 'Manajemen Akses',
              component: ManajemenAkses
            },
            {
              path: 'manajemen-role',
              name: 'Manajemen Role',
              component: ManajemenRole
            }
          ]
        },
        {
          path: 'laporan',
          redirect: '/laporan/perkara',
          name: 'Perkara',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'perkara',
              name: 'Perkara',
              component: Perkara
            },
            {
              path: 'produktivitas',
              name: 'Produktivitas',
              component: Produktivitas
            },
            {
              path: 'rekap',
              name: 'Rekap',
              component: Rekap
            }
          ]
        },
        {
          path: 'dpo-dpb',
          redirect: '/dpo-dpb/dpo',
          name: 'DPO',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'dpo',
              name: 'DPO',
              component: Dpo
            },
            {
              path: 'dpb',
              name: 'DPB',
              component: Dpb
            }
          ]
        },
        {
          path: 'kategori',
          redirect: '/kategori/layanan-darurat',
          name: 'Layanan Darurat',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'layanan-darurat',
              name: 'Layanan Darurat',
              component: LayananDarurat
            },
            {
              path: 'daftar-wilayah',
              name: 'Daftar Wilayah',
              component: DaftarWilayah
            },
            {
              path: 'struktur-organisasi',
              name: 'Struktur Organisasi',
              component: StrukturOrganisasi
            }
          ]
        },
      ]
    },
    {
      path: '/pages',
      redirect: '/pages/404',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '404',
          name: 'Page404',
          component: Page404
        },
        {
          path: '500',
          name: 'Page500',
          component: Page500
        },
        {
          path: 'login',
          name: 'Login',
          component: Login
        },
        {
          path: 'register',
          name: 'Register',
          component: Register
        }
      ]
    }
  ]
}

